.sidenav {
    
    margin-top: 100px;
    height:600px;
    width: 260px;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color:white;
    
    padding-top: 20px;
    border: 1px solid gray;
    overflow-y: scroll;
  }
  
  .sidenav a {
    padding: 6px 8px 6px 16px;
    text-decoration: none;
    font-size: 25px;
    color: #818181;
    display: block;
  }
  
  .sidenav a:hover {
    color: #f1f1f1;
  }
  
  
 